import { useRef, useState, useMemo } from "react"
import { PerspectiveCamera, OrbitControls, Stats } from "@react-three/drei"
import { useFrame } from '@react-three/fiber';
import * as THREE from 'three'
import Sun from "./planets/sun"
import Venus from "./planets/venus"
import Earth from "./planets/earth"
import CommonPlanet from "./planets/commonPlanet"
import PlanetMenu from "./planetMenu";

const planetesInfo = [{
	name: 'sun',
	description: 'Le Soleil est ltoile centrale du Systme solaire. Dans la classification astronomique, cest une toile de type naine jaune dune masse denviron 1,989  10^30 kg, compose dhydrogne (75 %) et dhlium (24 %) avec des traces dlments plus lourds, comme loxygne, le carbone, le non et le fer.'
}, {
	name: 'mercury',
	description: 'Plus proche du Soleil, Mercure est une petite plante rocheuse avec une surface crible de cratres. Elle na presque pas datmosphre et subit des tempratures extrmes.'
}, {
	name: 'venus',
	description: 'Surnomme la "jumelle" de la Terre en raison de sa taille, Vnus est couverte dpais nuages dacide sulfurique et connat un effet de serre dvastateur qui en fait la plante la plus chaude du systme solaire.'
}, {
	name: 'earth',
	description: 'Notre plante, unique pour abriter la vie, possde une atmosphre riche en oxygne, des ocans deau liquide et une biodiversit immense.'
}, {
	name: 'mars',
	description: 'La plante rouge, Mars, est connue pour ses vastes dserts de poussire, ses canyons, et ses volcans gants. Elle est un sujet dintrt majeur pour lexploration humaine.'
}, {
	name: 'jupiter',
	description: 'Jupiter est la plus grande plante du systme solaire. Cest une gante gazeuse avec des temptes massives, dont la clbre Grande Tache Rouge, qui fait rage depuis des sicles.'
}, {
	name: 'saturn',
	description: 'Saturne est une autre gante gazeuse, clbre pour son magnifique systme danneaux faits de glace et de roches. Cest lune des plus belles plantes  observer.',
	ring: true
}, {
	name: 'uranus',
	description: 'Uranus est une gante glace avec une inclinaison unique, tournant sur son ct. Son atmosphre bleu-vert est principalement compose dhydrogne, dhlium et de mthane.'
}, {
	name: 'neptune',
	description: 'Neptune, la plante la plus loigne du Soleil, est galement une gante glace. Elle possde des vents extrmement violents et une belle teinte bleue cause par la prsence de mthane dans son atmosphre.'
}]

export default function Experience()
{

	const [targetPlanet, setTargetPlanet] = useState(null)
	const [nameTargetPlanet, setNameTargetPlanet] = useState(null)
	const [cameraOnInitialPosition, setCameraOnInitialPosition] = useState(true)
	const orbitRef = useRef()
	const cameraRef = useRef()
	const planetRefs = {
		sun: useRef(),
		mercury: useRef(),
		venus: useRef(),
		earth: useRef(),
		mars: useRef(),
		jupiter: useRef(),
		saturn: useRef(),
		uranus: useRef(),
		neptune: useRef(),
	}

	const initialCameraPosition = useRef(new THREE.Vector3(0, 100, 500))

	const getOrbitalSpeed = (speed) => {
		return 0.0001 * speed
	}

	const getPlanetSize = (size) => {
		return size * 0.1
	}

	const handlePlanetClick = (planetCurrent, name) => {
		if (targetPlanet === planetCurrent.position) {
			resetCameraPosition();
		} else {
			setTargetPlanet(planetCurrent.position);
			setNameTargetPlanet(name);
			setCameraOnInitialPosition(false);
		}
  };	

	const handlePlanetPreviewClick = (name) => {
		const planetRef = planetRefs[name]
		const currentPlanet =	planetRef.current.getCurrent()
		setTargetPlanet(currentPlanet.position)
		setNameTargetPlanet(name)
		setCameraOnInitialPosition(false)
	}

	const resetCameraPosition = () => {
    setTargetPlanet(null);
		setNameTargetPlanet(null);
		setTimeout(() => {
			setCameraOnInitialPosition(true);
		}, 500)
  };

  useFrame(() => {
    if (targetPlanet && cameraRef.current) {
      // Dplace doucement la camra vers la plante slectionne
      cameraRef.current.position.lerp(
        targetPlanet.clone().add(new THREE.Vector3(0, 0, nameTargetPlanet === 'sun' ? 50 : 30 )), // Ajuste la distance de la camra par rapport  la plante
        0.1
      );

      // Fixer OrbitControls sur la plante
      if (orbitRef.current) {
        orbitRef.current.target.lerp(targetPlanet, 0.1); // La camra se fixe progressivement sur la plante
      }
    }

		if (!targetPlanet && cameraRef.current && !cameraOnInitialPosition) {
      cameraRef.current.position.lerp(initialCameraPosition.current, 0.1); // Retour progressif
      orbitRef.current.target.lerp(new THREE.Vector3(0, 0, 0), 0.1); // Remettre la cible des OrbitControls au centre
    }
  });

	return <>
		{!targetPlanet && <PlanetMenu planets={planetesInfo} handlePlanetPreviewClick={handlePlanetPreviewClick} />}
		<PerspectiveCamera ref={cameraRef} makeDefault position={[0, 200, 300]} fov={40} />
		<OrbitControls 
			maxDistance={500}
			minDistance={10}
			ref={orbitRef}
			enablePan
		/>
		
		<Sun 
			distance={0}
			size={useMemo(() => getPlanetSize(104), [])}
			rotationSpeed={0.001}
			orbitSpeed={0}
			onClick={handlePlanetClick}
			planeteInfo={planetesInfo[0]}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.sun}
			resetCameraPosition={resetCameraPosition}
		/>
		<CommonPlanet
			size={useMemo(() => getPlanetSize(2), [])}
			rotationSpeed={0.004}
			orbitSpeed={useMemo(() => getOrbitalSpeed(172), [])}
			planeteInfo={planetesInfo[1]}
			axisOne={19}
			axisTwo={22}
			onClick={handlePlanetClick}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.mercury}
			resetCameraPosition={resetCameraPosition}
		/>
		<Venus
			distance={30}
			size={useMemo(() => getPlanetSize(6), [])}
			rotationSpeed={0.003}
			orbitSpeed={useMemo(() => getOrbitalSpeed(126), [])}
			onClick={handlePlanetClick}
			planeteInfo={planetesInfo[2]}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.venus}
			resetCameraPosition={resetCameraPosition}
		/>
		<Earth
			distance={40}
			size={useMemo(() => getPlanetSize(6), [])}
			rotationSpeed={0.002}
			orbitSpeed={useMemo(() => getOrbitalSpeed(107), [])}
			onClick={handlePlanetClick}
			planeteInfo={planetesInfo[3]}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.earth}
			resetCameraPosition={resetCameraPosition}
		/>
		<CommonPlanet
			size={useMemo(() => getPlanetSize(3), [])}
			rotationSpeed={0.002}
			orbitSpeed={useMemo(() => getOrbitalSpeed(86), [])}
			planeteInfo={planetesInfo[4]}
			axisOne={53}
			axisTwo={55}
			onClick={handlePlanetClick}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.mars}
			resetCameraPosition={resetCameraPosition}
		/>
		<CommonPlanet
			size={useMemo(() => getPlanetSize(69), [])}
			rotationSpeed={0.001}
			orbitSpeed={useMemo(() => getOrbitalSpeed(47), [])}
			planeteInfo={planetesInfo[5]}
			axisOne={100}
			axisTwo={100}
			onClick={handlePlanetClick}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.jupiter}
			resetCameraPosition={resetCameraPosition}
		/>
		<CommonPlanet
			size={useMemo(() => getPlanetSize(58), [])}
			rotationSpeed={0.001}
			orbitSpeed={useMemo(() => getOrbitalSpeed(34), [])}
			planeteInfo={planetesInfo[6]}
			axisOne={160}
			axisTwo={160}
			onClick={handlePlanetClick}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.saturn}
			resetCameraPosition={resetCameraPosition}
		/>
		<CommonPlanet
			size={useMemo(() => getPlanetSize(25), [])}
			rotationSpeed={0.001}
			orbitSpeed={useMemo(() => getOrbitalSpeed(24), [])}
			planeteInfo={planetesInfo[7]}
			axisOne={250}
			axisTwo={250}
			onClick={handlePlanetClick}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.uranus}
			resetCameraPosition={resetCameraPosition}
		/>
		<CommonPlanet
			size={useMemo(() => getPlanetSize(24), [])}
			rotationSpeed={0.001}
			orbitSpeed={useMemo(() => getOrbitalSpeed(19), [])}
			planeteInfo={planetesInfo[8]}
			axisOne={300}
			axisTwo={300}
			onClick={handlePlanetClick}
			nameTargetPlanet={nameTargetPlanet}
			ref={planetRefs.neptune}
			resetCameraPosition={resetCameraPosition}
		/>
		<color attach="background" args={["#000"]} />
	</>
}